import React from 'react';

import logo from '../../assets/logo.svg';
import sub from '../../assets/sub.svg';

import * as S from './styles';

export default function Home() {
  return (
    <React.Fragment>
      <S.Sidebar />
      <div className="sidebar">
        <a className="logo" rel="index" href=".">
          <img src={logo} alt="Wordpress Developer, Web Developer, Front End Developer Logo" />
          <img
            top="20px"
            src={sub}
            alt="Wordpress Developer, Web Developer , Front End Developer"
          />
        </a>
        <nav className="en">
          <a rel="index" href="./" className="home-link active">
            <S.IconHome />
          </a>
          <a rel="about" href="./about/" className="">
            <S.IconUser />
          </a>
          <a rel="skills" href="./skills/" className="">
            <S.IconSettings />
          </a>
          <a rel="gallery" href="./portfolio/" className="">
            <S.IconEye />
          </a>
          <a rel="contact" href="./contact/" className="">
            <S.IconMail />
          </a>
        </nav>
        <ul>
          <li>
            <a
              id="github"
              href="https://github.com/williamjesusti"
              rel="noopener noreferrer"
              target="_blank"
            >
              <S.IconGithub />
            </a>
          </li>
          <li>
            <a
              id="linkedin"
              href="https://linkedin.com/in/williamjesusti"
              rel="noopener noreferrer"
              target="_blank"
            >
              <S.IconLinkedin />
            </a>
          </li>
          <li>
            <a
              id="facebook"
              href="https://www.facebook.com/WillZinhOZ"
              rel="noopener noreferrer"
              target="_blank"
            >
              <S.IconFacebook />
            </a>
          </li>
          <li>
            <a
              id="instagram"
              href="https://instagram.com/willzinhoz"
              rel="noopener noreferrer"
              target="_blank"
            >
              <S.IconInstagram />
            </a>
          </li>
        </ul>
      </div>
    </React.Fragment>
  );
}
